/* @import-normalize; */

* {
  box-sizing: border-box;
}

body {
 background-color: rgb(0, 0, 0);

  font-family: Arial, Helvetica, sans-serif;
}

ul, 
ol {
  padding: 0;
  margin: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}